@import url(https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap);
.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.question-container {
    margin: 1rem 20rem;
    display: flex;
}


.options-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    grid-row-gap: 1rem;
    row-gap: 1rem;
}

.option {
    height: 2rem;
    width: 2rem;
    background-color: rgb(72, 155, 233);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
}

.option.correct:active {
    background-color: greenyellow;
}

.option.wrong:active {
    background-color: red;
}


.main-container > h1 {
    font-weight: 100;
    text-transform: uppercase;
    font-size: 1rem;
    text-align: center;
    margin: 1rem 0 0;
}

.main-container {
    margin-bottom: 2rem;
}
.clef > .line {
    margin: 0.3rem 0;
}

.line {
    width: 3rem;
    height: 0.1rem;
    background-color: black;
}

.clef {
    position: relative;
}
.note {
    width: 0.45rem;
    height: 0.45rem;
    border-radius: 50%;
    background-color: black;
    position: absolute;
    left: 1.2rem;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 250%;
  font-family: 'Lato', sans-serif;
}

body {
  font-size: 1rem;
}
