.main-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


.question-container {
    margin: 1rem 20rem;
    display: flex;
}


.options-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 1rem;
    row-gap: 1rem;
}

.option {
    height: 2rem;
    width: 2rem;
    background-color: rgb(72, 155, 233);
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
}

.option.correct:active {
    background-color: greenyellow;
}

.option.wrong:active {
    background-color: red;
}


.main-container > h1 {
    font-weight: 100;
    text-transform: uppercase;
    font-size: 1rem;
    text-align: center;
    margin: 1rem 0 0;
}

.main-container {
    margin-bottom: 2rem;
}